<template>
<el-row class="tac">
    <el-col :span="24">
        <el-menu :router="true" default-active="2" class="el-menu-vertical-demo" background-color="" text-color="#fff" active-text-color="#ffd04b">
            <el-menu-item index="/home/Pos" v-if="!isAdmin"> <i class="el-icon-s-order"></i>收银</el-menu-item>
            <el-menu-item v-if="isAdmin" index="/home/store"> <i class="el-icon-s-shop"></i>餐桌</el-menu-item>
            <el-menu-item v-if="isAdmin" index="/home/goods"> <i class="el-icon-present"></i>商品</el-menu-item>
            <el-menu-item v-if="isAdmin" index="/home/member"> <i class="el-icon-s-custom"></i>会员管理</el-menu-item>
            <el-menu-item index="/home/employee" v-if="isAdmin"> <i class="el-icon-s-order"></i>员工管理</el-menu-item>
            <el-menu-item v-if="isAdmin" index="/home/statistics"> <i class="el-icon-s-data"></i>统计</el-menu-item>
            <el-menu-item v-if="isAdmin" index="/home/setAdmin"> <i class="el-icon-setting"></i>设置</el-menu-item>
        </el-menu>
    </el-col>
</el-row>
</template>

<style scoped>
.el-menu-item{
    background-color:steelblue !important;
}
</style>

<script>
export default {
    data(){
        return{
            isAdmin:this.$store.state.isAdmin,
        }
    }
}
</script>



